import randomInt from './randomInt'

// aphorism : -> string
// Produces a random aphrism randomly.
const aphorism = () => {
  const list =
    [ ' ,  .'
    ,   '  . '
      + '      . '
      + '          , '
      + '           '
      + ' .'
    ,   '     ,   ,      '
      + '      .     '
      + " ,        ' ', "
      + "            ."
    ,   ',    .     '
      + '     .'
    ,   '"     ?"  .  '
      + '".  .    ."  '
      + '"  ?"  '
      + '   .'
    ]

  return list[randomInt(list.length)]
}

export default aphorism



